.steps-container {
    display: block;
    width: 75%;
    max-width: 500px;
    min-height: 20px;
    margin-top: 25px;
    margin-right: auto;
    margin-left: auto;
}
@media (max-width: 400px){
    .steps-container {
        width: 90%;
    }
    .steps-row {
        width: 90% !important;
    }
}

.steps-container .step-line {
    display: inline-block;
    width: 33.3333%;
    height: 2px;
    margin-bottom: 11px;
    background-color: #fff;
}

.steps-container .step {
    display: inline-block;
    width: 25px;
    height: 25px;
    border: 2px solid #fff;
    border-radius: 50px;
    cursor: pointer;
}

.steps-container .step.active {
    background-color: #fff;
}

.steps-row {
    width: 75%;
    max-width: 500px;
    min-height: 20px;
    margin-top: 10px;
    margin-right: auto;
    margin-left: auto;
}

.steps-row .step-title {
    display: inline-block;
    font-size: 10px;
    letter-spacing: 2px;
    text-transform: uppercase;
}


.pay-options-container .flex.item {
    padding: 15px 0;
    border-bottom: 2px solid #eee;
    text-align: left;
    -ms-flex-align: center;
        align-items: center;
}

.pay-options-container .flex.item div {
    margin-left: 15px;
}

.pay-options-container .flex.item div img {
    width: 30px;
    height: 30px;
}

.pay-options-container h5 {
    color: #999;
    font-size: 20px;
    margin: 10px 0;
}

/* .card-info-container {
    margin-top: 50px;
} */

.checkout-details-container p {
    color: #aaa;
    font-size: 12px;
}

.checkout-details-container h5 {
    font-size: 18px;
}

.checkout-details-container .address-txt {
    font-size: 14px;
    color: #333;
    font-weight: 600;
}

.thank-you-container .inner {
    max-width: 500px;
    padding: 0 30px;
}

@media (max-width: 500px){
    .thank-you-container .inner .card .grid {
        display: block;
    }
}


.credit-cards-container {
    margin-top: 40px;
}

.credit-cards-container .payment-method {
    width: 100%;
    margin-bottom: 20px;
    -ms-flex-align: center;
        align-items: center;
    padding: 15px;
    border-radius: 3px;
    -webkit-box-shadow: 0px 4px 20px 3px rgba(168,168,168,0.3);
    box-shadow: 0px 4px 20px 3px rgba(168,168,168,0.3);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.credit-cards-container .payment-method img {
    width: 40px;
    display: block;
    height: auto;
}

.credit-cards-container .new-card-selector {
    color: #999;
    border-radius: 4px;
    border: 2px dashed #d6d6d6;
    padding: 20px;
    cursor: pointer;
}
.credit-cards-container .new-card-selector i {
    display: block;
    margin-bottom: 5px;
    font-size: 30px;
}

.credit-cards-container .new-card-selector span {
    font-size: 14px;
}

.selected-card {
    border-radius: 6px;
    border: 2px solid #51aef8;
    padding: 20px 15px;
    margin-bottom: 15px;
}

.selected-card img {
    max-width: 100%;
}

.selected-card .cvv-wrapper {
    display: -ms-inline-flexbox;
    display: inline-flex;
}
.selected-card .cvv-wrapper .w-input-container {
    margin: 0;
}
.selected-card .cvv-wrapper .w-input-container.hasError {
    margin: 0;
    padding-top: 13px;
}

.selected-card .cvv-wrapper input {
    margin: 0;
    padding: 3px 0;
    height: auto;
    width: 80px;
}

.selected-card .card-details span {
    margin-right: 12px;
    -ms-flex-item-align: center;
        align-self: center;
    font-size: 14px;
}


@media (max-width: 600px){
    .steps-row {
        width: 80%;
    }
}

@media (max-width: 500px){
    .billing-container .grid {
        display: block;
    }
    .card-info-container .grid {
        display: block;
    }
}

@media (max-width: 600px){
    .nutrition-info .item {
        margin-bottom: 5px !important;
    }
}

.guest-checkout.em-modal .em-divider {
    height: 2px;
    background: #ccc;
    margin: 20px 0;
}




