.action-btn {
    cursor: pointer;
    position: relative;
    border: 1px solid #c4cdd5;
    padding: 5px 10px;
    background: -webkit-gradient(linear,left top, left bottom,from(#fff),to(#f9fafb));
    background: -webkit-linear-gradient(top,#fff,#f9fafb);
    background: -o-linear-gradient(top,#fff,#f9fafb);
    background: linear-gradient(180deg,#fff,#f9fafb);
    cursor: pointer;
    -webkit-box-shadow: 0 1px 0 0 rgba(22,29,37,.05);
    box-shadow: 0 1px 0 0 rgba(22,29,37,.05);
    border-radius: 3px;
    color: #212b36;
    text-align: center;
    font-size: 14px;
}

.action-btns-container .actions-modal {
    top: 45px !important;
    padding: 12px 0;
    min-width: 250px;
    position: absolute;
    z-index: 400;
    left: 0;
    border-radius: 4px;
    border: 1px solid #c4cdd5;
    background: #fff;
    -webkit-box-shadow: 0 0 0 1px rgba(6,44,82,.1), 0 2px 16px rgba(33,43,54,.08);
    box-shadow: 0 0 0 1px rgba(6,44,82,.1), 0 2px 16px rgba(33,43,54,.08);
}
.action-btns-container .actions-modal ul {
    list-style: none;
    text-align: left;
    margin: 0;
    padding: 0;
}
.action-btns-container .actions-modal ul li {
    display: block;
    cursor: pointer;
    padding: 10px 16px;
    font-weight: 300;
}
.action-btns-container .actions-modal ul li:hover {
    background:#3ea345;
    color: white;
}
.pagination-container {
    padding-top: 10px;
}
.pagination-container ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: -ms-inline-flexbox;
    display: inline-flex;
    border: 1px solid #c4cdd5;
    border-radius: 3px;
}
.pagination-container ul li {
    list-style: none;
    padding: 0;
    display: inline-block;
    cursor: pointer;
    background: -webkit-gradient(linear, left top, left bottom, from(white), to(#f9fafb));
    background: -webkit-linear-gradient(top, white, #f9fafb);
    background: -o-linear-gradient(top, white, #f9fafb);
    background: linear-gradient(to bottom, white, #f9fafb);
    color: black;
}
.pagination-container ul li a {
    display: inline-block;
    padding: 10px 15px;
    color: black;
}
.pagination-container ul li.disabled {
    background: #f4f6f8;
    color: #919eab;
    cursor: default;
    pointer-events: none;
}
.pagination-container ul li.disabled a {
    color: #919eab;
}
.pagination-container ul li:first-child {
    border-right: 1px solid #c4cdd5;
}
.pagination-container ul li i {
    position: relative;
    top: 1px;
    font-size: 20px;
}
.pagination-container ul li:first-child i {
    right: -5px;
}
.pagination-container ul li:last-child i {
    right: -1px;
}

.select-default {
    font-size: 14px;
    padding: 4px 16px;
    height: 36px;
    border-color: #c4cdd5;
    background: -webkit-gradient(linear, left top, left bottom, from(white), to(#f9fafb));
    background: -webkit-linear-gradient(top, white, #f9fafb);
    background: -o-linear-gradient(top, white, #f9fafb);
    background: linear-gradient(to bottom, white, #f9fafb);
    color: #637381;
}

.apply-btn, .em-btn-std {
    background: #3ea345;
    -webkit-box-shadow: 1px 1px 7px 0 rgba(0, 0, 0, .1);
            box-shadow: 1px 1px 7px 0 rgba(0, 0, 0, .1);
    color: white;
    cursor: pointer;
    padding: 10px 15px;
    border-radius: 1px;
    font-size: 14px;
}
.em-btn-std {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
}



